import type { Board } from 'chessboard/model/board/types';

/**
 * Determines if a square is on the edge of the board
 */
export function isEdgeSquare(square: Board.SanSquare): boolean {
  if (!square) {
    return false;
  }

  const edgeValues = ['a', 'h', '1', '8'];
  let isEdge = false;

  edgeValues.forEach((value) => {
    if (square.includes(value)) {
      isEdge = true;
    }
  });

  return isEdge;
}
