import type { Board } from 'chessboard/model/board/types';
import { Game } from 'chessboard/model/game/types';
import { Core } from 'chessboard/core/config/types';
import { setTestData, TestElements, TestKeys } from 'src/utils/set-test-data';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import { BoardPlaceholders } from 'chessboard/renderers/default/board/types';
import { Pointerdown } from 'chessboard/model/dom/get-pointer-events';
import { createCoreEvent } from 'chessboard/core/model/create-core-event';
import { getColorAsLetter } from 'chessboard/model/game/get-color-as-letter';
import { isRightButton } from 'chessboard/model/dom/is-right-button';
import { ChessError } from 'chessboard/model/error';
import { ErrorCodes } from 'chessboard/model/error/types';

/**
 * Handlers related to the promotion window.
 */
export function createPromotionWindowMethods({
  board,
  options,
}: {
  board: BoardDOM;
  el: HTMLElement;
  options: Board.Options;
}) {
  let promotionWindow: HTMLElement;
  let closeButton: HTMLElement;
  const pieces: { [key: string]: HTMLElement | undefined } = {
    b: undefined,
    n: undefined,
    q: undefined,
    r: undefined,
  };

  return {
    closePromotionWindow,
    openPromotionWindow,
  };

  /**
   * Create the promotion window and add it to the DOM.
   */
  function createPromotionWindow(): void {
    promotionWindow = document.createElement('div');

    promotionWindow.classList.add('promotion-window');

    if (options.test) {
      setTestData(promotionWindow, {
        [TestKeys.Element]: TestElements.PromotionWindow,
      });
    }

    board.addToDom({ el: promotionWindow, type: BoardPlaceholders.PromotionWindow });
  }

  /**
   * Create the close button and attach it to the promotion window.
   */
  function createCloseButton(run: Core.Run): void {
    closeButton = document.createElement('i');

    closeButton.className = 'close-button icon-font-chess x';

    if (options.test) {
      setTestData(closeButton, {
        [TestKeys.Element]: TestElements.PromotionCloseButton,
      });
    }

    promotionWindow.appendChild(closeButton);

    closeButton.addEventListener(Pointerdown, (e) => {
      e.stopPropagation();
      run(createCoreEvent(Core.BoardEvents.PromotionAreaClosePointerdown));
    });
  }

  /**
   * Create the pieces and attach to the promotion window.
   */
  function createPieces(run: Core.Run): void {
    Object.keys(pieces).forEach((pieceType) => {
      const pieceEl = document.createElement('div');

      pieceEl.addEventListener(Pointerdown, (e) => {
        e.stopPropagation();

        if (isRightButton(e)) {
          run(createCoreEvent(Core.UserEvents.PointerdownRight));

          return;
        }

        run(
          createCoreEvent(Core.BoardEvents.PromotionPiecePointerdown, {
            piece: pieceType,
          }),
        );
      });

      pieceEl.classList.add('promotion-piece');

      pieces[pieceType] = pieceEl;

      if (options.test) {
        setTestData(<HTMLElement>pieces[pieceType], {
          [TestKeys.Element]: TestElements.PromotionPiece,
          [TestKeys.Type]: pieceType,
        });
      }

      promotionWindow.appendChild(pieceEl);
    });
  }

  /**
   * Get details about the promotion window's required position and color.
   */
  function getPromotionWindowDetails({
    flipped,
    promotionMove,
  }: {
    flipped: boolean;
    promotionMove: Game.Move.Model;
  }): {
    color: Game.ColorsAsNumbers;
    file: number;
    position: string;
  } {
    let position: string;

    if (flipped) {
      position = promotionMove.color === Game.ColorsAsNumbers.White ? 'bottom' : 'top';
    } else {
      position = promotionMove.color === Game.ColorsAsNumbers.White ? 'top' : 'bottom';
    }

    const color = promotionMove.color;

    const files = flipped ? 'hgfedcba' : 'abcdefgh';

    const file = files.indexOf(promotionMove.to.slice(0, 1)) + 1;

    return {
      color,
      file,
      position,
    };
  }

  /**
   * Open the promotion window (creates it if necessary).
   */
  function openPromotionWindow(promotionMove: Game.Move.Model, context: Core.Context): void {
    const {
      options: { flipped },
      run,
    } = context;

    const { color, file, position } = getPromotionWindowDetails({ flipped, promotionMove });

    if (!promotionWindow) {
      createPromotionWindow();
    }

    if (!closeButton) {
      createCloseButton(run);
    }

    if (!pieces.q) {
      createPieces(run);
    }

    setPieceOrder(position);

    setPromotionWindowFile(file);

    setPieceColors(color);

    if (options.test) {
      setTestData(promotionWindow, {
        [TestKeys.Color]: getColorAsLetter(color),
        [TestKeys.File]: file.toString(),
        [TestKeys.Position]: position,
      });
    }

    showPromotionWindow();
  }

  /**
   * Close the promotion window.
   */
  function closePromotionWindow(): void {
    if (!promotionWindow) {
      throw new ChessError({
        code: ErrorCodes.ElementNotFound,
        message: 'Promotion window does not exist.',
      });
    }

    promotionWindow.style.display = 'none';
  }

  /**
   * Set the color of the pieces.
   */
  function setPieceColors(color: Game.ColorsAsNumbers): void {
    Object.keys(pieces).forEach((pieceType) => {
      (<HTMLElement>pieces[pieceType]).className = `promotion-piece ${getColorAsLetter(
        color,
      )}${pieceType}`;

      if (options.test) {
        setTestData(<HTMLElement>pieces[pieceType], {
          [TestKeys.Color]: getColorAsLetter(color),
        });
      }
    });
  }

  /**
   * Set the order of the pieces.
   */
  function setPieceOrder(position: string): void {
    if (position === 'top') {
      promotionWindow.classList.add('top');
    } else {
      promotionWindow.classList.remove('top');
    }
  }

  /**
   * Set the file of the promotion window.
   */
  function setPromotionWindowFile(file: number): void {
    promotionWindow.style.transform = `translateX(${(file - 1) * 100}%`;
  }

  /**
   * Show the promotion window.
   */
  function showPromotionWindow(): void {
    promotionWindow.style.display = '';
  }
}
