import { Board } from 'chessboard/model/board/types';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import type {
  AnimationStep,
  AnimationStepsGenerators,
  PieceAnimation,
} from 'chessboard/renderers/default/piece-movement/animations/types';
import { AnimationTypes } from 'chessboard/renderers/default/piece-movement/animations/types';
import { generateFadeOutSteps } from 'chessboard/renderers/default/piece-movement/animations/generate-fade-out-steps';
import { generateSlideSteps } from 'chessboard/renderers/default/piece-movement/animations/generate-slide-steps';

const FRAME_LENGTH_MS = 16;

/**
 * Create an interface to add and flush animations.
 * Animations are always flushed before adding new ones.
 */
export function createAnimations(board: BoardDOM) {
  let isLastStep: boolean;
  const steps: any[] = [];

  const generateSteps: AnimationStepsGenerators = {
    [AnimationTypes.FadeOut]: generateFadeOutSteps,
    [AnimationTypes.Slide]: generateSlideSteps,
  };

  return {
    add,
    flush,
    run,
  };

  /**
   * Add an array of animations and start them.
   */
  function add(animations: PieceAnimation[], opts: Board.Options) {
    const { animationType, flipped } = opts;

    const numSteps = Math.max(Math.floor(getDuration(animationType) / FRAME_LENGTH_MS), 1);

    // This produces the following structure:
    // [[firstAnimStep#1, firstAnimStep#2, firstAnimStep#3], [secondAnimStep#1, secondAnimStep#2, secondAnimStep#3]]
    const animationsSteps = animations.map((animation) =>
      generateSteps[animation.type]({
        animation,
        isFlipped: flipped || false,
        numSteps,
      }),
    );

    animationsSteps.forEach((animation) => {
      if (animation.length > steps.length) {
        const fillerArray = Array(animation.length - steps.length).fill([]);
        steps.unshift(...fillerArray);
      }

      // eslint-disable-next-line max-params
      animation.forEach((step, ndx, arr) => {
        const ndxFromEnd = arr.length - ndx;

        steps[steps.length - ndxFromEnd] = [...steps[steps.length - ndxFromEnd], step];
      });
    });
  }

  /**
   * Flush all animations synchronously.
   */
  function flush() {
    if (steps.length === 0) {
      isLastStep = true;
      return;
    }

    // Remove all but the last step
    steps.splice(0, steps.length - 1);

    run();
  }

  /**
   * Run through the steps and apply CSS properties to elements.
   */
  function run() {
    isLastStep = steps.length < 2;

    const nextStep = steps.shift();

    if (!nextStep) {
      return;
    }

    if (!board.isAnimating()) {
      board.setAnimatingStatus(true);
    }

    nextStep.forEach((step: AnimationStep) => {
      Object.entries(step.style).forEach(([prop, value]) => {
        (<any>step.el.style)[prop] = value;
      });

      // Some steps may have callbacks. For instance if we're trying to fade-out
      // a piece, then we will need to remove it after the last step, so the
      // fadeOut animation adds a callback to remove the element.
      if (step.callback) {
        step.callback();
      }
    });

    if (steps.length === 0) {
      board.setAnimatingStatus(false);
    }

    if (!isLastStep) {
      requestAnimationFrame(() => {
        run();
      });
    }
  }
}

/**
 * Get the animation duration according to the animation type.
 */
function getDuration(type: Board.Animation.Types) {
  switch (type) {
    case Board.Animation.Types.Slow:
      return Board.Animation.Speeds.Slow;
    case Board.Animation.Types.Fast:
      return Board.Animation.Speeds.Fast;
    case Board.Animation.Types.None:
      return 0;
    default:
      return Board.Animation.Speeds.Default;
  }
}
