import { ThemeAssets } from 'chessboard/config/theme-assets';
import type { Board } from 'chessboard/model/board/types';
import { defaultLegacyPieceStyles } from './legacy-themes';
import type { LegacyThemes } from './legacy-themes';

/**
 * Determine if piece style is pseudo 3d for default renderer.
 */
export function isPseudo3d(
  boardOptions: Board.Options,
  pieceStyles: LegacyThemes.PieceStyles = defaultLegacyPieceStyles,
): boolean {
  const result = boardOptions.themeAssets
    ? boardOptions.themeAssets.config.perspective === ThemeAssets.Perspective.PSEUDO_3D
    : pieceStyles[boardOptions.pieceStyle].isPseudo3d;

  return Boolean(result);
}
