import type { Board } from 'chessboard/model/board/types';
import { flippedKnightArrowSlopes } from 'chessboard/config/arrows';
import { getKnightArrowPoints } from 'chessboard/renderers/default/arrows/create-arrow/get-knight-arrow-points';
import { getKnightArrowRotation } from 'chessboard/model/arrows/get-knight-arrow-rotation';

/**
 * Create a default arrow
 */
export function createKnightArrow({
  from,
  polygon,
  slope,
  to,
}: {
  from: Board.Coords;
  polygon: SVGPolygonElement;
  slope: number;
  to: Board.Coords;
}): SVGPolygonElement {
  let transform = `rotate(${getKnightArrowRotation({ from, slope, to })} ${from.x} ${from.y})`;

  if (flippedKnightArrowSlopes.includes(slope)) {
    transform += ` scale(-1, 1) translate(-${2 * from.x}, 0)`;
  }

  polygon.setAttribute('transform', transform);
  polygon.setAttribute('points', getKnightArrowPoints({ from }));

  return polygon;
}
