import { getSanFromFileRank } from 'model/board/get-san-from-file-rank';
import type { Game } from 'model/game/types';
import { getColorAsNumber } from 'model/game/get-color-as-number';

/**
 * Format the DOM pieces as an object, for comparison with the API.
 */
export function formatPiecesAsObject(pieces: HTMLElement[]): Game.Piece.ConciseObject {
  const piecesObject = {};

  pieces.forEach((piece) => {
    const classNames = piece.className.split(' ');

    const squareClassName = classNames.find((className) => className.startsWith('square-'));
    const fullShortString = classNames.find(
      (className) => className.startsWith('w') || className.startsWith('b'),
    );

    if (!squareClassName || !fullShortString) {
      return;
    }

    const [color, type] = fullShortString;

    const file = parseInt(squareClassName[squareClassName.length - 2], 10);
    const rank = parseInt(squareClassName[squareClassName.length - 1], 10);

    const san = getSanFromFileRank({ file, rank });

    if (!san) {
      return;
    }

    piecesObject[san] = {
      color: getColorAsNumber(<Game.ColorsAsLetters>color),
      type,
    };
  });

  return piecesObject;
}
