import 'chessboard/renderers/default/coordinates/style.scss';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import { BoardPlaceholders } from 'chessboard/renderers/default/board/types';
import { Board } from 'chessboard/model/board/types';
import { setTestData, TestElements, TestKeys } from 'src/utils/set-test-data';
import { getTextNodes } from 'chessboard/renderers/default/coordinates/get-text-nodes';
import type { ThemeAssets } from 'chessboard/config/theme-assets';

/**
 * Create coordinates.
 */
export function createCoordinatesMethods({
  board,
  options,
}: {
  board: BoardDOM;
  options: Board.Options;
}) {
  return {
    setCoordinates,
  };

  /**
   * Add coordinates.
   */
  function addCoordinates(type: Board.CoordinatesPositions, boardIsFlipped: boolean) {
    const svg = createCoordinates(type, boardIsFlipped, options);

    board.addToDom({ el: svg, type: BoardPlaceholders.Coordinates });
  }

  /**
   * Add coordinates to the DOM.
   */
  function setCoordinates({
    flipped,
    position,
  }: {
    flipped: boolean;
    position: Board.CoordinatesPositions;
  }): void {
    removeCoordinates();

    if (position === Board.CoordinatesPositions.Off) {
      return;
    }

    addCoordinates(position, flipped);
  }

  /**
   * Remove coordinates from the DOM.
   */
  function removeCoordinates(): void {
    const coordinatesEl = board.el.querySelector('.coordinates');

    if (!coordinatesEl) {
      return;
    }

    (<HTMLElement>coordinatesEl.parentNode).removeChild(coordinatesEl);
  }
}

/**
 * Creates the SVG element for the coordinates.
 */
export function createCoordinates(
  type: Board.CoordinatesPositions,
  boardIsFlipped: boolean,
  options: Board.Options<ThemeAssets.Assets>,
  style?: string,
): SVGSVGElement {
  const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
  svg.setAttribute('viewBox', '0 0 100 100');
  svg.classList.add('coordinates');

  if (type === Board.CoordinatesPositions.Outside) {
    svg.classList.add('outside');
  }

  if (options.test) {
    setTestData(svg, {
      [TestKeys.Element]: TestElements.Coordinates,
      [TestKeys.Flipped]: boardIsFlipped.toString(),
      [TestKeys.Position]: type,
    });
  }

  svg.innerHTML = '';

  if (style) {
    svg.innerHTML += style;
  }

  svg.innerHTML += getTextNodes(type, boardIsFlipped);
  return svg;
}
