import type { Board } from 'chessboard/model/board/types';
import type { LegacyThemes } from 'chessboard/config/legacy-themes';
import {
  defaultLegacyBoardStyles,
  defaultLegacyPieceStyles,
  shortPieceNames,
} from 'chessboard/config/legacy-themes';
import { getLegacyBoardURL } from 'chessboard/model/board/get-legacy-board-url';
import { getLegacyPieceBaseURL } from 'chessboard/model/board/get-legacy-piece-base-url';
import defaultPiecesBase64 from 'chessboard/renderers/default/assets/default-pieces-base64.json';
import { ThemeAssets } from 'chessboard/config/theme-assets';

const TRANSITION_DURATION_MS = 300;
export const TRANSITION_STYLE = `transition: background-image ${TRANSITION_DURATION_MS}ms linear;`;

/**
 * Create the DOM for the style tag and expose an API for it.
 */
export function createStyleDOM({
  boardStyles,
  options,
  pieceStyles,
}: {
  boardStyles: LegacyThemes.BoardStyles;
  options: Board.Options<ThemeAssets.Assets2D>;
  pieceStyles: LegacyThemes.PieceStyles;
}) {
  updateStyles(options);

  return {
    destroy,
    getStyleEl,
    getPieceStyles: () => pieceStyles,
    updateStyles,
  };

  /**
   * Remove style tag.
   */
  function destroy(): void {
    const styleEl = document.getElementById(getStyleId());

    if (!styleEl || !styleEl.parentNode) {
      return;
    }

    styleEl.parentNode.removeChild(styleEl);
  }

  /**
   * Gets the style element itself.
   */
  function getStyleEl(): HTMLElement | null {
    return document.getElementById(getStyleId());
  }

  /**
   * Get the id for the style tag for this board.
   */
  function getStyleId(): string {
    return options.useSharedStyleTag ? 'board-styles-shared' : `board-styles-${<string>options.id}`;
  }

  /**
   * Generate the styles for the board, ready to be inserted into the style tag.
   * Relies on legacy boardStyle and pieceStyle options
   */
  function getLegacyStyleInnerHTML(opts: Board.Options, fallback = false): string {
    const { boardStyle, id, pieceStyle } = opts;
    const { path, format, isPseudo3d } = pieceStyles[pieceStyle] ?? defaultLegacyPieceStyles.neo;
    const pieceBaseUrl = getLegacyPieceBaseURL(path, undefined, opts);
    const boardTheme = boardStyles[boardStyle] ?? defaultLegacyBoardStyles.green;
    const pseudoElement = isPseudo3d ? '::after' : '';

    const pieceStylesString = !fallback
      ? shortPieceNames.reduce(
          (acc, shortString) =>
            `${acc}#board-${id} .piece.${shortString}${pseudoElement}, #board-${id} .promotion-piece.${shortString}${pseudoElement} {
            background-image: url('${pieceBaseUrl}/${shortString}.${format}');
          }`,
          '',
        )
      : shortPieceNames.reduce(
          (acc, shortString) =>
            `${acc}#board-${id} .piece.${shortString}, #board-${id} .promotion-piece.${shortString} {
            background-image: url('${defaultPiecesBase64[shortString]}');
          }`,
          '',
        );

    const boardStylesString = `
      #board-${id}, .fade-in-overlay {
        background-image: url('${getLegacyBoardURL(boardTheme[2], undefined, opts)}');
      }
      .coordinate-light {
        fill: ${boardTheme[0]};
      }
      .coordinate-dark {
        fill: ${boardTheme[1]};
      }
      .highlight {
        background-color: ${boardTheme[4]};
      }
    `;

    const transitionStyles = `#board-${id}, #board-${id} .piece {
      ${TRANSITION_STYLE}
    }`;

    return boardStylesString + pieceStylesString + transitionStyles;
  }

  /**
   * Generate the styles for the board, ready to be inserted into the style tag.
   */
  function getStyleInnerHTML(opts: Board.Options<ThemeAssets.Assets2D>, fallback = false): string {
    const { id, themeAssets } = opts;
    if (!themeAssets) {
      return getLegacyStyleInnerHTML(opts, fallback);
    }

    const pseudoElement =
      themeAssets.config.perspective === ThemeAssets.Perspective.PSEUDO_3D ? '::after' : '';

    const pieceStylesString = Object.keys(themeAssets.pieces.assets).reduce(
      (acc, pieceString) =>
        `${acc}#board-${id} .piece.${pieceString}${pseudoElement}, #board-${id} .promotion-piece.${pieceString}${pseudoElement} {
        background-image: url('${themeAssets.pieces.assets[pieceString]}');
      }`,
      '',
    );

    const boardStylesString = `
      #board-${id}, .fade-in-overlay {
        background-image: url('${themeAssets.board.assets.background}');
      }
      .coordinate-light {
        fill: ${themeAssets.board.config.lightSquareCoordinateHex};
      }
      .coordinate-dark {
        fill: ${themeAssets.board.config.darkSquareCoordinateHex};
      }
      .highlight {
        background-color: ${themeAssets.board.config.highlightSquareHex};
      }
    `;

    const transitionStyles = `#board-${id}, #board-${id} .piece {
      ${TRANSITION_STYLE}
    }`;

    return boardStylesString + pieceStylesString + transitionStyles;
  }

  /**
   * Update the style tag according to the current options. Creates the style
   * tag if it doesn't exist.
   */
  function updateStyles(opts: Board.Options<ThemeAssets.Assets2D>, fallback = false): void {
    const styleId = getStyleId();
    let styleEl = getStyleEl();

    if (!styleEl) {
      styleEl = document.createElement('style');

      (<HTMLStyleElement>styleEl).type = 'text/css';
      styleEl.id = styleId;

      const head = (<HTMLDocument>document).head;

      if (head) {
        head.appendChild(styleEl);
      }
    }

    const themeStyles = getStyleInnerHTML(opts, fallback);

    if (styleEl.innerHTML !== themeStyles) {
      styleEl.innerHTML = themeStyles;

      // Remove transition styles after animation is complete.
      // Context: piece elements are reused on position change
      // and we only want a transition animation when updating the theme.
      setTimeout(() => {
        if (!styleEl) {
          return;
        }
        styleEl.innerHTML = styleEl.innerHTML.replace(TRANSITION_STYLE, '');
      }, TRANSITION_DURATION_MS + 50);
    }
  }
}
