import 'chessboard/renderers/default/fade-setup/style.scss';
import type { Board } from 'chessboard/model/board/types';
import type { Core } from 'chessboard/core/config/types';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import { BoardPlaceholders } from 'chessboard/renderers/default/board/types';
import { setTestData, TestKeys, TestElements } from 'src/utils/set-test-data';

/**
 * Create coordinates.
 */
export function createFadeSetupMethods({ board }: { board: BoardDOM }) {
  let fadeInOverlay: HTMLElement | undefined;

  return {
    fadeSetup,
  };

  /**
   * Adds in the overlay div
   */
  function createFadeInOverlay(options: Board.Options) {
    fadeInOverlay = document.createElement('div');

    fadeInOverlay.classList.add('fade-in-overlay');

    board.addToDom({ el: fadeInOverlay, type: BoardPlaceholders.FadeSetup });

    fadeInOverlay.addEventListener('animationend', removeAnimation);

    if (options.test) {
      setTestData(fadeInOverlay, {
        [TestKeys.Element]: TestElements.FadeInOverlay,
      });
    }
  }

  /**
   * Removes a preexisting animation
   */
  function removeAnimation() {
    if (fadeInOverlay) {
      fadeInOverlay.style.animationDuration = '';
      fadeInOverlay.classList.remove('animate');
    }
  }

  /**
   * Removes the overlay div
   */
  function removeFadeInOverlay() {
    if (fadeInOverlay) {
      fadeInOverlay.remove();
    }

    // remove memory reference and thereby remove animationend listener
    fadeInOverlay = undefined;
  }

  /**
   * Add fadeSetup.
   */
  function fadeSetup({ options }: Core.Context) {
    // Exit early if the fade setup is 0
    if (options.fadeSetup === 0) {
      // if a preexisting fadeCover element exists and the API is falling back
      // to 0, we need to remove the DOM element
      if (fadeInOverlay) {
        removeFadeInOverlay();
      }

      return;
    }

    if (!fadeInOverlay) {
      createFadeInOverlay(options);
    }

    // from now on fadeInOverlay is defined but typescript doesn't know
    if (fadeInOverlay) {
      fadeInOverlay.style.animationDuration = `${options.fadeSetup / 1000}s`;
      fadeInOverlay.classList.add('animate');
    }
  }
}
