import type {
  Animations,
  PieceAnimation,
} from 'chessboard/renderers/default/piece-movement/animations/types';
import { AnimationTypes } from 'chessboard/renderers/default/piece-movement/animations/types';
import type { Board } from 'chessboard/model/board/types';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import { ChessError } from 'chessboard/model/error';
import { createAnimations } from 'chessboard/renderers/default/piece-movement/animations';
import { createElementPool } from 'src/utils/dom/element-pool/element-pool';
import { createPiece } from 'chessboard/renderers/default/piece-movement/create-piece';
import { createCollection } from 'utils/create-collection/create-collection';
import { ErrorCodes } from 'chessboard/model/error/types';
import type { EventEmitterUtil } from '@chesscom/web-ui';
import type { Game } from 'chessboard/model/game/types';
import type {
  Piece,
  PieceMove,
  Pieces,
  PieceCollection,
} from 'chessboard/renderers/default/piece-movement/types';
import { PieceEvents } from 'chessboard/renderers/default/piece-movement/types';
import { nodeTargetSquare } from 'api/move/model/node-target-square';

/**
 * Create an object that holds the pieces.
 */
export function createPieces({
  board,
  emitter,
  options,
}: {
  board: BoardDOM;
  emitter: EventEmitterUtil.EventEmitter;
  options: Board.Options;
}): Pieces {
  const elementPool = createElementPool({
    insertBefore: board.placeholders.pieces,
    startingCount: 32,
  });
  const animations: Animations = createAnimations(board);
  let draggingSquare: Board.SanSquare | undefined;
  const pieces: PieceCollection = createCollection<Piece, Board.SanSquare>();

  return {
    animations,
    create,
    destroy,
    get,
    getDraggingSquare,
    move,
    remove,
    setDraggingState,
    setPositionByCoords,
    setPositionBySquare,
    suspendOverSquare,
  };

  /**
   * Create a piece, and add it to the DOM.
   */
  function create(pieceToCreate: Game.Piece.Model): Piece {
    const piece: Piece = createPiece({
      details: pieceToCreate,
      el: elementPool.get(),
      emitter,
      test: options.test,
    });

    pieces.set(<Board.SanSquare>pieceToCreate.square, piece);

    emitter.emit(PieceEvents.PieceShown, { ...pieces.get(<Board.SanSquare>pieceToCreate.square) });

    return piece;
  }

  /**
   * Destroy the pieces.
   */
  function destroy() {
    elementPool.destroy();
  }

  /**
   * Get the piece on a square.
   */
  function get(square?: Board.SanSquare, shouldThrow: boolean = true) {
    if (!square) {
      return pieces;
    }

    if (!pieces.isDefined(square) && shouldThrow) {
      throw new ChessError({
        code: ErrorCodes.ElementNotFound,
        data: { square },
        message: 'Piece does not exist.',
      });
    }

    return pieces.isDefined(square) ? pieces.get(square) : undefined;
  }

  /**
   * Returns the dragging square.
   */
  function getDraggingSquare(): Board.SanSquare | undefined {
    return draggingSquare;
  }

  /**
   * Move a piece or pieces.
   */
  function move(moves: PieceMove | (PieceMove | undefined)[], opts: Board.Options): void {
    const gameMoves: (PieceMove | undefined)[] = Array.isArray(moves) ? moves : [moves];

    const gameToMoves = gameMoves
      .filter(Boolean)
      .map((gameMove) => nodeTargetSquare(<PieceMove>gameMove));

    // Cache the piece objects before we start moving them around.
    const moveFromPieces: Piece[] = gameMoves
      .filter(Boolean)
      .map((gameMove) => <Piece>get((<PieceMove>gameMove).from));

    const anims: PieceAnimation[] = [];

    gameMoves.forEach((gameMove, ndx) => {
      if (!gameMove) {
        throw new ChessError({
          code: ErrorCodes.ElementNotFound,
          message: 'Move object does not exist.',
        });
      }

      const squareTo = nodeTargetSquare(gameMove);

      if (!squareTo) {
        return;
      }

      // Set the position first. This ensures that when the animations are flushed,
      // the piece will have the correct position. As the next animation code is
      // synchronous, the actual transform position set here is overwritten in the
      // same execution cycle.
      (<Piece>moveFromPieces[ndx]).setPositionBySquare(squareTo);

      if (gameMove.animate) {
        anims.push({
          el: (<Piece>moveFromPieces[ndx]).el,
          from: gameMove.from,
          to: squareTo,
          type: AnimationTypes.Slide,
        });
      }

      // Update pieces cache
      pieces.set(squareTo, moveFromPieces[ndx]);

      if (!gameToMoves.includes(<Board.SanSquare>gameMove.from)) {
        pieces.deleteItem(<Board.SanSquare>gameMove.from);
      }

      if (gameMove.promotion) {
        pieces.get(squareTo).setDetails(<Game.ColorsAsNumbers>gameMove.color, gameMove.promotion);
      }
    });

    if (anims.length) {
      animations.add(anims, opts);
    }
  }

  /**
   * Remove a piece from the DOM.
   */
  function remove({
    animate,
    options: opts,
    squares,
  }: {
    squares: Board.SanSquare[];
    options: Board.Options;
    animate?: boolean;
  }): void {
    const anims: PieceAnimation[] = [];

    squares.forEach((square) => {
      if (!pieces.isDefined(square)) {
        return;
      }

      const el = (<Piece>get(square)).el;

      if (animate) {
        anims.push({
          callback: removeElement,
          el: (<Piece>get(square)).el,
          type: AnimationTypes.FadeOut,
        });
      } else {
        removeElement();
      }

      pieces.deleteItem(square);

      /**
       * Remove an element
       */
      function removeElement(): void {
        if (!el) {
          throw new ChessError({
            code: ErrorCodes.ElementNotFound,
            data: { square },
            message: 'Piece does not exist. Cannot remove.',
          });
        }

        elementPool.put(el);
        emitter.emit(PieceEvents.PieceHidden, square);
      }
    });

    if (anims.length) {
      animations.add(anims, opts);
    }

    // Ensure dragging state is unset if it was the dragging piece that was removed.
    if (draggingSquare && !pieces.isDefined(draggingSquare)) {
      draggingSquare = undefined;
    }
  }

  /**
   * Raises a piece up above the rest with z-index.
   */
  function setDraggingState(square: Board.SanSquare, drag?: boolean): void {
    if (drag && square === draggingSquare) {
      return;
    }
    const piece: Piece = <Piece>get(square);

    piece.setDraggingState(drag);

    draggingSquare = drag ? square : undefined;
  }

  /**
   * Set the position of a piece by coordinates.
   */
  function setPositionByCoords(square: Board.SanSquare, coords: Board.Coords): void {
    const piece: Piece = <Piece>get(square);

    const currentDraggingSquare = getDraggingSquare();

    if (currentDraggingSquare && currentDraggingSquare !== square) {
      setDraggingState(currentDraggingSquare, false);
    }

    piece.setPositionByCoords(coords);
  }

  /**
   * Set the position of a piece by square.
   */
  function setPositionBySquare(square: Board.SanSquare, to: Board.SanSquare): void {
    const piece: Piece = <Piece>get(square);

    if (to !== square) {
      pieces.set(to, pieces.get(square));
      pieces.deleteItem(square);
    }

    piece.setPositionBySquare(to);
  }

  /**
   * Suspend a piece over a square.
   */
  function suspendOverSquare(square: Board.SanSquare, to: Board.SanSquare): void {
    const piece: Piece = <Piece>get(square);

    piece.setPositionBySquare(to, true);
  }
}
