import type { Board } from 'chessboard/model/board/types';
import { calculateArrowRotation } from 'chessboard/model/arrows/calculate-arrow-rotation';
import { getStraightArrowPoints } from 'chessboard/renderers/default/arrows/create-arrow/get-straight-arrow-points';

/**
 * Create a default arrow
 */
export function createStraightArrow({
  from,
  polygon,
  to,
}: {
  from: Board.Coords;
  polygon: SVGPolygonElement;
  to: Board.Coords;
}): SVGPolygonElement {
  const arrowRotation = calculateArrowRotation({ from, to });

  polygon.setAttribute('transform', `rotate(${arrowRotation} ${from.x} ${from.y})`);
  polygon.setAttribute('points', getStraightArrowPoints({ from, to }));

  return polygon;
}
