import { arrowStyles } from 'chessboard/config/arrows';
import type { Board } from 'chessboard/model/board/types';
import { calculateArrowLength } from 'chessboard/model/arrows/calculate-arrow-length';

const halfArrowWidth = arrowStyles.WIDTH / 2;
const arrowHeadHeight = arrowStyles.HEAD_HEIGHT;
const tailPadding = arrowStyles.TAIL_PADDING;
const halfArrowHeadWidth = arrowStyles.HEAD_WIDTH / 2;

/**
 * Get the point value for the SVG polygon points attribute
 */
export function getStraightArrowPoints({
  from,
  to,
}: {
  from: Board.Coords;
  to: Board.Coords;
}): string {
  const length = calculateArrowLength({ from, to });

  return `
    ${from.x - halfArrowWidth} ${from.y + tailPadding},
    ${from.x - halfArrowWidth} ${from.y + length - arrowHeadHeight},
    ${from.x - halfArrowHeadWidth} ${from.y + length - arrowHeadHeight},
    ${from.x} ${from.y + length},
    ${from.x + halfArrowHeadWidth} ${from.y + length - arrowHeadHeight},
    ${from.x + halfArrowWidth} ${from.y + length - arrowHeadHeight},
    ${from.x + halfArrowWidth} ${from.y + tailPadding}
  `.trim();
}
