import type { Board } from 'chessboard/model/board/types';
import { Game } from 'chessboard/model/game/types';

/**
 * Get the color for an arrow. First choice is one of the color options, like alt
 * or shift. Next is custom color. Last is the default color option.
 */
export function getArrowColor(
  color: Game.Node.Marking.Arrow.Model['color'],
  colorOptions: Board.ArrowColors,
): string {
  return (
    (color && colorOptions[Game.Node.Marking.JCEColorsMap[color]]) ?? color ?? colorOptions.default
  );
}
