import type { Board } from 'chessboard/model/board/types';
import { calculateArrowLength } from 'chessboard/model/arrows/calculate-arrow-length';

/**
 * Get the angle to rotate the arrow
 */
export function calculateArrowRotation({
  from,
  to,
}: {
  from: Board.Coords;
  to: Board.Coords;
}): number {
  const length = calculateArrowLength({ from, to });

  // arrows point straight down before they get rotated. So we need to calculate
  // the coordinates of the original `to` point from before the arrow is rotated.
  const originalTo = {
    x: from.x,
    y: from.y + length,
  };

  // arrow before rotation
  const vector1 = {
    x: from.x - originalTo.x,
    y: from.y - originalTo.y,
  };

  // arrow after rotation
  const vector2 = {
    x: from.x - to.x,
    y: from.y - to.y,
  };

  // calculate the angle between the two vectors
  const cosine = (vector1.x * vector2.x + vector1.y * vector2.y) / length ** 2;
  const clampedCosine = Math.max(-1, Math.min(1, cosine));
  const radians = Math.acos(clampedCosine);

  // convert to degrees and round to 100th
  const degrees = Math.floor(radians * (180 / Math.PI) * 100) / 100;

  return from.x < to.x ? 360 - degrees : degrees;
}
