import type { Board } from 'chessboard/model/board/types';
import type { Game } from 'chessboard/model/game/types';
import { getOppositeColor } from 'chessboard/model/game/get-opposite-color';
import { nodeTargetSquare } from 'api/move/model/node-target-square';

/**
 * Get the details required for undoing a move.
 */
export function getUndoMoveDetails(move: Game.Move.ExtendedNode): {
  movedPieces: Game.Move.Undo[];
  promotedSquare?: Board.SanSquare;
  restoredPiece?: Game.Piece.Model;
} {
  const movedPieces: Game.Move.Undo[] = [
    {
      animate: move.animate,
      from: nodeTargetSquare(move),
      to: <Board.SanSquare>move.from,
    },
  ];

  if (move.rookMove) {
    movedPieces.push({
      ...move.rookMove,
      from: move.rookMove.to,
      to: move.rookMove.from,
    });
  }

  const restoredPiece =
    move.captured && move.capturedStr
      ? {
          color: getOppositeColor(move.color),
          promoted: move.capturedPromotedPawn,
          square: move.EPCapturedSquare || move.to,
          type: <Game.Piece.ShortStrings.Black>move.capturedStr.toLowerCase(),
        }
      : undefined;

  return {
    movedPieces,
    promotedSquare: move.promotion ? <Board.SanSquare>move.from : undefined,
    restoredPiece: <Game.Piece.Model>restoredPiece,
  };
}
