import type { Board } from 'chessboard/model/board/types';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import { BoardPlaceholders } from 'chessboard/renderers/default/board/types';
import { setPositionBySquare } from 'chessboard/model/board/set-position-by-square';
import { setTestData, TestKeys, TestElements } from 'src/utils/set-test-data';

/**
 * Handlers related to the hover square.
 */
export function createHoverSquareMethods({
  board,
  options,
}: {
  board: BoardDOM;
  options: Board.Options;
}) {
  const hoverSquareEl: HTMLElement = createHoverSquareEl();
  let hoveredSquare: Board.SanSquare | undefined;

  return {
    hideHoverSquare,
    showHoverSquare,
  };

  /**
   * Show the hover square.
   */
  function showHoverSquare(square: Board.SanSquare) {
    if (hoveredSquare === square) {
      return;
    }

    hoverSquareEl.style.visibility = '';

    if (options.test) {
      setTestData(hoverSquareEl, {
        [TestKeys.Element]: TestElements.HoverSquare,
      });
    }

    setPositionBySquare({ el: hoverSquareEl, square });
    hoveredSquare = square;
  }

  /**
   * Hide the hover square.
   */
  function hideHoverSquare() {
    if (hoverSquareEl) {
      hoverSquareEl.style.visibility = 'hidden';
      hoveredSquare = undefined;
    }
  }

  /**
   * Create the hover square element
   */
  function createHoverSquareEl() {
    const el = document.createElement('div');
    const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');

    svg.setAttribute('viewBox', '0 0 100 100');
    svg.innerHTML =
      '<rect x="0" y="0" width="100" height="100" stroke="rgba(255, 255, 255, 0.65)" stroke-width="10" fill="none"/>';

    el.append(svg);

    el.classList.add('hover-square');
    el.style.visibility = 'hidden';

    board.addToDom({ el, type: BoardPlaceholders.HoverSquare });

    if (options.test) {
      setTestData(el, {
        [TestKeys.Element]: TestElements.HoverSquare,
      });
    }

    return el;
  }
}
