import type { Board } from 'chessboard/model/board/types';

/**
 * Get a SanSquare from a FileRank.
 */
export function getSanFromFileRank(fileRank: Board.FileRank): Board.SanSquare | null {
  if (
    !fileRank ||
    !fileRank.file ||
    !fileRank.rank ||
    fileRank.file > 8 ||
    fileRank.rank > 8 ||
    fileRank.file < 1 ||
    fileRank.rank < 1
  ) {
    return null;
  }

  return <Board.SanSquare>`${String.fromCharCode(96 + fileRank.file)}${fileRank.rank.toString()}`;
}
