import type { Board } from 'chessboard/model/board/types';
import type { KeyedBlinkingHighlight } from 'chessboard/plugins/highlights/config/types';
import { HighlightType } from 'chessboard/plugins/highlights/config/types';

/**
 * Create a blinking square for when the king is in check.
 */
export function createCheckBlinkingHighlight(
  square: Board.SanSquare,
  color: string,
): KeyedBlinkingHighlight {
  return {
    data: { color, interval: 500, opacity: 0.5, square, times: 3 },
    key: square,
    type: HighlightType,
  };
}
