import { Game } from 'chessboard/model/game/types';

/**
 * Gets the color as a number
 */
export function getColorAsNumber(color: Game.ColorsAsLetters | Game.ColorsAsWords) {
  return color === Game.ColorsAsLetters.Black || color === Game.ColorsAsWords.Black
    ? Game.ColorsAsNumbers.Black
    : Game.ColorsAsNumbers.White;
}
