import type { Board } from 'chessboard/model/board/types';

/**
 * Ensures effect element is within the chessboard.
 */
export function tuckMarking({
  square,
  isFlipped,
  effectEl,
}: {
  square: Board.SanSquare;
  isFlipped: boolean;
  effectEl: HTMLDivElement;
}): void {
  const tuckPosition = getTuckPosition(square, isFlipped);

  if (tuckPosition.top) {
    effectEl.classList.add('tuck-top');
  }

  if (tuckPosition.right) {
    effectEl.classList.add('tuck-right');
  }
}

export type TuckInfo = {
  top: boolean;
  right: boolean;
};

/**
 * Find available tuck position.
 */
export function getTuckPosition(square: Board.SanSquare, isFlipped: boolean): TuckInfo {
  const sides = {
    top: isFlipped ? '1' : '8',
    right: isFlipped ? 'a' : 'h',
  };

  return {
    top: square.includes(sides.top),
    right: square.includes(sides.right),
  };
}
