import type { Board } from 'chessboard/model/board/types';

export interface Animations {
  add: (animations: PieceAnimation[], options: Board.Options) => void;
  flush: () => void;
  run: () => void;
}

export enum AnimationTypes {
  Slide,
  FadeOut,
}

export type Callback = (() => void) | undefined;

export interface PieceAnimation {
  callback?: Callback;
  el: HTMLElement;
  from?: Board.SanSquare;
  to?: Board.SanSquare;
  type: AnimationTypes;
}

export interface AnimationStep {
  callback?: Callback;
  el: HTMLElement;
  style: Record<string, string>;
}

export type AnimationStepsGenerators = {
  [key in AnimationTypes]: ({
    animation,
    isFlipped,
    numSteps,
  }: {
    animation: PieceAnimation;
    isFlipped: boolean;
    numSteps: number;
  }) => AnimationStep[];
};
