import { getCoordsFromSquare } from 'chessboard/renderers/default/get-coords-from-square';
import type {
  AnimationStep,
  PieceAnimation,
} from 'chessboard/renderers/default/piece-movement/animations/types';

/**
 * Generate steps for a sliding animation.
 */
export function generateSlideSteps({
  animation,
  isFlipped = false,
  numSteps,
}: {
  animation: PieceAnimation;
  isFlipped: boolean;
  numSteps: number;
}): AnimationStep[] {
  const { el, from, to } = animation;

  if (!from || !to) {
    return [];
  }

  const fromCoords = getCoordsFromSquare(from, isFlipped);
  const toCoords = getCoordsFromSquare(to, isFlipped);

  if (!fromCoords || !toCoords) {
    return [];
  }

  const xStep = (toCoords.x - fromCoords.x) / numSteps;
  const yStep = (toCoords.y - fromCoords.y) / numSteps;

  return Array(numSteps)
    .fill(undefined)
    .map((_, ndx) => {
      const isLastStep = ndx === numSteps - 1;

      // If we're at not at the last step
      const coords = !isLastStep
        ? {
            // Coords are the fromCoords + the diff
            x: fromCoords.x + xStep * (ndx + 1),
            y: fromCoords.y + yStep * (ndx + 1),
          }
        : toCoords; // Otherwise it's just to toCoords

      return {
        el,
        style: {
          transform: isLastStep ? '' : `translate(${coords.x}%, ${coords.y}%)`,
          zIndex: isLastStep ? '' : '10',
        },
      };
    });
}
