import type { Board } from 'chessboard/model/board/types';
import { Game } from 'chessboard/model/game/types';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import type { Core } from 'chessboard/core/config/types';
import { createCheckBlinkingHighlight } from 'chessboard/renderers/default/highlights/create-check-blinking-highlight';
import { createPieces } from 'chessboard/renderers/default/piece-movement/create-pieces';
import type { DragPiece } from 'chessboard/renderers/config/types';
import type { EventEmitterUtil } from '@chesscom/web-ui';
import { isCastle } from 'chessboard/model/move/is-castle';
import { getUndoMoveDetails } from 'chessboard/renderers/model/get-undo-move-details';
import type {
  Pieces,
  Piece,
  PieceMove,
  PieceCollection,
} from 'chessboard/renderers/default/piece-movement/types';
import { nodeTargetSquare } from 'api/move/model/node-target-square';

/**
 * Handlers related to piece movement.
 */
export function createPieceMovementMethods({
  board,
  emitter,
  options,
}: {
  board: BoardDOM;
  emitter: EventEmitterUtil.EventEmitter;
  options: Board.Options;
}) {
  const pieces: Pieces = createPieces({ board, emitter, options });

  return {
    destroy: pieces.destroy,
    dragPiece,
    dropPiece,
    illegalMove,
    loadPieces,
    makeMove,
    removePiece,
    undoMove,
  };

  /**
   * Drag a piece.
   */
  function dragPiece(p: DragPiece) {
    if (!p) {
      return;
    }

    if (p.toSquare) {
      pieces.suspendOverSquare(p.square, p.toSquare);
    } else if (p.coords) {
      pieces.setPositionByCoords(p.square, p.coords);
    }

    pieces.setDraggingState(p.square, true);
  }

  /**
   * Drop a piece.
   */
  function dropPiece() {
    const currentDraggingSquare = pieces.getDraggingSquare();

    if (!currentDraggingSquare) {
      return;
    }

    pieces.setPositionBySquare(currentDraggingSquare, currentDraggingSquare);

    pieces.setDraggingState(currentDraggingSquare, false);
  }

  /**
   * Respond to an illegal move.
   */
  function illegalMove(kingInCheck: false | Board.SanSquare, context: Core.Context): void {
    if (kingInCheck) {
      const { renderer } = context;

      renderer?.blinkHighlights(
        [createCheckBlinkingHighlight(kingInCheck, options.checkBlinkingSquareColor)],
        context,
      );
    }
  }

  /**
   * Make a move.
   */
  function makeMove(move: Game.Move.Extended, context: Core.Context): void {
    pieces.animations.flush();

    if (move.drop) {
      pieces.create(<Game.Piece.Model>{
        color: move.color as Game.ColorsAsNumbers,
        square: nodeTargetSquare(move),
        type: move.piece as Game.Piece.ShortStrings.Black,
      });

      return;
    }

    if (move.EPCapturedSquare) {
      pieces.remove({
        animate: move.animate,
        options: context.options,
        squares: [move.EPCapturedSquare],
      });
    } else if (pieces.get(move.to, /* Don't throw if not found */ false) && !isCastle(move)) {
      pieces.remove({ animate: move.animate, options: context.options, squares: [move.to] });
    }

    const moves: PieceMove[] = [move, <Game.Move.Extended>move.rookMove].filter(Boolean);

    pieces.move(moves, context.options);

    pieces.animations.run();
  }

  /**
   * Load a new set of pieces onto the board, reusing the DOM where possible.
   */
  function loadPieces(params: { pieces: Game.Piece.Collection }, context: Core.Context): void {
    const piecesToLoad = params.pieces;

    pieces.animations.flush();

    const squaresToLoad = piecesToLoad.keys();
    const unneeded = (<PieceCollection>pieces.get())
      .keys()
      .filter((square) => !squaresToLoad.includes(square));

    // Remove any that no longer exist
    pieces.remove({
      options: context.options,
      squares: unneeded,
    });

    // Add or update any remaining pieces
    piecesToLoad.keys().forEach((square) => {
      const pieceToLoad: Game.Piece.Model = piecesToLoad.get(square);

      // Don't throw error here.
      const piece = pieces.get(square, false);

      // Add it if it doesn't already exist
      if (!piece) {
        pieces.create(pieceToLoad);
        return;
      }

      const { type, color } = (<Piece>piece).getDetails();

      // Update it if it's changed
      if (type !== pieceToLoad.type || color !== pieceToLoad.color) {
        (<Piece>piece).setDetails(pieceToLoad.color, pieceToLoad.type);
      }
    });

    pieces.animations.run();
  }

  /**
   * Remove a piece.
   */
  function removePiece(square: Board.SanSquare, context: Core.Context) {
    pieces.remove({ options: context.options, squares: [square] });
  }

  /**
   * Undo the last move.
   */
  function undoMove(move: Game.Move.ExtendedNode, context: Core.Context): void {
    pieces.animations.flush();

    if (move.drop) {
      removePiece(move.to, context);
      return;
    }

    const { movedPieces, restoredPiece, promotedSquare } = getUndoMoveDetails(move);

    pieces.move(movedPieces, context.options);

    if (restoredPiece) {
      pieces.create(restoredPiece);
    }

    if (promotedSquare) {
      const promotedPiece: Piece = <Piece>pieces.get(promotedSquare);

      const { color } = promotedPiece.getDetails();

      promotedPiece.setDetails(color, Game.Piece.Types.Pawn);
    }

    pieces.animations.run();
  }
}
