import type { Board } from 'chessboard/model/board/types';
import { getSanFromFileRank } from 'chessboard/model/board/get-san-from-file-rank';
import { getCoordsInsideBoard } from 'chessboard/renderers/default/get-coords-inside-board';
import { getSquareInsideBoard } from 'chessboard/renderers/default/get-square-inside-board';
import { getXYFromEvent } from 'src/utils/get-xy-from-event';

/**
 * Get a pointer position relative to the board, from a pointer event,
 * assuming a 2D board. This is the default pointer position formatter.
 */
export function getPointerPosition({
  el,
  event,
  flipped,
}: {
  el: HTMLElement;
  event: any;
  flipped: boolean;
}) {
  const elRect: ClientRect = el.getBoundingClientRect();
  const { x, y } = getXYFromEvent(event);

  const squareSize = elRect.width / 8;

  const coordsInsideSquare: Board.Coords = {
    x: Math.round((x - elRect.left) % squareSize),
    y: Math.round((y - elRect.top) % squareSize),
  };

  const nonFlippedFile = Math.ceil((x - elRect.left) / squareSize);
  const nonFlippedRank = Math.ceil((elRect.bottom - y) / squareSize);

  const file = flipped ? 9 - nonFlippedFile : nonFlippedFile;
  const rank = flipped ? 9 - nonFlippedRank : nonFlippedRank;

  const square: Board.SanSquare | null = getSanFromFileRank({
    file,
    rank,
  });

  const coords = <Board.Coords>{
    x: ((x - elRect.left - squareSize / 2) / elRect.width) * 800,
    y: ((y - elRect.top - squareSize / 2) / elRect.height) * 800,
  };

  const coordsInsideBoard = getCoordsInsideBoard(coords);
  const squareInsideBoard = getSquareInsideBoard(file, rank);

  return {
    coords,
    coordsInsideBoard,
    coordsInsideSquare,
    square,
    squareInsideBoard,
  };
}
