import type { Board } from 'chessboard/model/board/types';
import type { Core } from 'chessboard/core/config/types';
import type { Renderer } from 'chessboard/renderers/config/types';

export interface FromToCoords {
  fromCoords: Board.Coords | undefined;
  toCoords: Board.Coords | undefined;
}
export interface BoardDOM {
  animationComplete: Renderer.Model['animationComplete'];
  addToDom: ({
    type,
    el,
    insertAfter,
  }: {
    type: BoardPlaceholders;
    el: Comment | HTMLElement | SVGSVGElement;
    insertAfter?: boolean;
  }) => void;
  el: HTMLElement;
  flipBoard: (shouldFlip: boolean, context?: Core.Context) => void;
  setBoardEnabled: (enabled: boolean) => void;
  isAnimating: Renderer.Model['isAnimating'];
  isFlipped: () => boolean;
  reset: () => void;
  placeholders: { [key in BoardPlaceholders]: Comment };
  setAnimatingStatus: (isAnimating: boolean) => void;
}

export enum BoardPlaceholders {
  Arrows = 'arrows',
  BlinkingHighlights = 'blinking-highlights',
  CaptureHints = 'capture-hints',
  Coordinates = 'coordinates',
  Effects = 'effects',
  FadeSetup = 'fade-setup',
  HoverSquare = 'hover-square',
  MoveHints = 'move-hints',
  Pieces = 'pieces',
  PromotionWindow = 'promotion-window',
  Squares = 'squares',
}
