import type { Animations } from 'chessboard/renderers/default/piece-movement/animations/types';
import type { Board } from 'chessboard/model/board/types';
import type { Game } from 'chessboard/model/game/types';
import type { Collection } from 'utils/create-collection/types';
import type JCE from '@chesscom/js-chess-engine';

export interface PieceDetails {
  color: Game.ColorsAsNumbers;
  type: Game.Piece.ShortStrings.Black;
}

export interface Piece {
  el: HTMLElement;
  getDetails: () => PieceDetails;
  setDetails: (color: Game.ColorsAsNumbers, type: Game.Piece.ShortStrings.Black) => void;
  setDraggingState: (drag?: boolean) => void;
  setPositionByCoords: (coords: Board.Coords) => void;
  setPositionBySquare: (to: Board.SanSquare, suspend?: boolean) => void;
}

export type PieceCollection = Collection<Piece, Board.SanSquare>;

export const enum PieceEvents {
  Created = 'Created',
  DetailsSet = 'DetailsSet',
  DragEnded = 'DragEnd',
  DragStarted = 'DragStart',
  PieceShown = 'PieceShown',
  PieceHidden = 'PieceHidden',
  PositionSetBySquare = 'PositionSetBySquare',
}

export interface PieceMove {
  animate?: boolean;
  captured?: number | JCE.CasedPieceSignature;
  color?: Game.ColorsAsNumbers;
  from?: Board.SanSquare;
  kingTo?: Board.SanSquare;
  promotion?: Game.Piece.ShortStrings.Black;
  to: Board.SanSquare;
}

export interface Pieces {
  animations: Animations;
  create: (piece: Game.Piece.Model) => Piece;
  destroy: () => void;
  get: (square?: Board.SanSquare, shouldThrow?: boolean) => Piece | PieceCollection | undefined;
  getDraggingSquare: () => Board.SanSquare | undefined;
  move: (moves: PieceMove | (PieceMove | undefined)[], options: Board.Options) => void;
  remove: ({
    animate,
    options,
    squares,
  }: {
    squares: Board.SanSquare[];
    options: Board.Options;
    animate?: boolean;
  }) => void;
  setDraggingState: (square: Board.SanSquare, drag?: boolean) => void;
  setPositionByCoords: (square: Board.SanSquare, coords: Board.Coords) => void;
  setPositionBySquare: (square: Board.SanSquare, to: Board.SanSquare) => void;
  suspendOverSquare: (square: Board.SanSquare, to: Board.SanSquare) => void;
}
