import type {
  AnimationStep,
  Callback,
  PieceAnimation,
} from 'chessboard/renderers/default/piece-movement/animations/types';

/**
 * Fades out from 1 to 0 linearly, but only after 75% of the duration
 * i.e. the fade out is delayed.
 */
export function generateFadeOutSteps({
  animation,
  numSteps,
}: {
  animation: PieceAnimation;
  numSteps: number;
}): AnimationStep[] {
  return (
    Array(numSteps)
      .fill(undefined)
      // eslint-disable-next-line
      .map((_, ndx, arr) => {
        let callback: Callback;

        const stepPercentage = (ndx + 1) / numSteps;
        let opacity = 2.86 - 2.86 * stepPercentage;

        opacity = opacity > 1 ? 1 : opacity;

        if (ndx === arr.length - 1 && animation.callback) {
          callback = animation.callback;
        }

        return {
          callback,
          el: animation.el,
          style: {
            opacity: opacity.toString(),
          },
        };
      })
  );
}
