import type { Board } from 'chessboard/model/board/types';

/**
 * Get degrees to rotate knight arrow
 */
export function getKnightArrowRotation({
  from,
  slope,
  to,
}: {
  from: Board.Coords;
  slope: number;
  to: Board.Coords;
}): number {
  switch (slope) {
    case 2:
      return from.x > to.x ? 180 : 0;
    case -2:
      return from.x > to.x ? 0 : 180;
    case 0.5:
      return from.x > to.x ? 90 : 270;
    case -0.5:
      return from.x > to.x ? 90 : 270;
    default:
      return 0;
  }
}
