import { getFileRankFromSan } from 'chessboard/model/board/get-file-rank-from-san';
import type JCE from '@chesscom/js-chess-engine';
import type { Board } from './types';

/**
 * Get the file rank taking into account a flipped board. So if the square is a1, then
 * when it's flipped, the file/rank will be 8/8.
 */
export function getAbsoluteFileRankFromSan(
  square: JCE.SanSquare,
  flipped: boolean,
): Board.FileRank {
  const fileRank = getFileRankFromSan(square);

  return flipped ? { file: 9 - fileRank.file, rank: 9 - fileRank.rank } : fileRank;
}
