import 'chessboard/renderers/default/style.scss';
import type { CreateRenderer, RendererExtensionParams } from 'chessboard/renderers/config/types';
import { Renderer } from 'chessboard/renderers/config/types';
import { getDefaultOptions } from 'chessboard/model/board/get-default-options';
import { createAnalysisOverlayMethods } from 'chessboard/renderers/default/analysis-overlay';
import { createArrowsMethods } from 'chessboard/renderers/default/arrows';
import { createAssetsMethods } from 'chessboard/renderers/default/assets';
import { createBoard } from 'chessboard/renderers/default/board';
import { createCoordinatesMethods } from 'chessboard/renderers/default/coordinates';
import { createFadeSetupMethods } from 'chessboard/renderers/default/fade-setup';
import { createHoverSquareMethods } from 'chessboard/renderers/default/hover-square';
import { createMoveHintsMethods } from 'chessboard/renderers/default/move-hints';
import { createPieceMovementMethods } from 'chessboard/renderers/default/piece-movement';
import { createPromotionWindowMethods } from 'chessboard/renderers/default/promotion-window';
import { createHighlightsMethods } from 'chessboard/renderers/default/highlights';
import { getPointerPosition } from 'chessboard/renderers/default/get-pointer-position';
import { getCoordsFromSquare } from 'chessboard/renderers/default/get-coords-from-square';
import {
  defaultLegacyBoardStyles,
  defaultLegacyPieceStyles,
} from 'chessboard/config/legacy-themes';
import { formatPiecesAsObject } from 'chessboard/renderers/default/format-pieces-as-object';
import { EventEmitterUtil } from '@chesscom/web-ui';
import { validateBoardOptions2D } from 'chessboard/config/theme-validation-utils';

const { createEventEmitter } = EventEmitterUtil;

export { createDefaultRenderer as createRenderer };

/* eslint-disable max-params */
/**
 * Creates a Renderer for the default HTML board.
 */
function createDefaultRenderer({
  el,
  emitter = createEventEmitter(),
  options = getDefaultOptions(),
  boardStyles = defaultLegacyBoardStyles,
  pieceStyles = defaultLegacyPieceStyles,
  testElement,
}: CreateRenderer.Options): Renderer.Model {
  const board = createBoard({ el, options, testElement });

  const params: RendererExtensionParams = { board, el, emitter, options };
  const assetsMethods = createAssetsMethods({
    ...params,
    boardStyles,
    pieceStyles,
    options: validateBoardOptions2D(options),
  });
  const pieceMethods = createPieceMovementMethods(params);

  if (board.isFlipped()) {
    board.flipBoard(true);
  }

  assetsMethods.togglePseudo3d(options);

  const methods = {
    // Analysis overlay
    ...createAnalysisOverlayMethods(params),

    // Arrows
    ...createArrowsMethods(params),

    // Assets
    ...assetsMethods,

    // Coordinates
    ...createCoordinatesMethods(params),

    // FadeSetup
    ...createFadeSetupMethods(params),

    // Hover Square
    ...createHoverSquareMethods(params),

    // Move Hints
    ...createMoveHintsMethods(params),

    // Highlights
    ...createHighlightsMethods(params),

    // PromotionWindow
    ...createPromotionWindowMethods(params),

    // Pieces
    ...pieceMethods,

    animationComplete: board.animationComplete,

    areAssetsLoaded: () => true,

    createRenderer: createDefaultRenderer,

    destroy,

    ...emitter,

    extendRenderer,

    flipBoard: board.flipBoard,

    setBoardEnabled: board.setBoardEnabled,

    getCoordsFromSquare,

    getPieces,

    getPointerPosition,

    getRendererOptions: () => ({
      boardStyles,
      el,
      options,
      pieceStyles,
      emitter,
    }),

    getBoardContainerAspectRatio: () => 1,

    isAnimating: board.isAnimating,

    name: Renderer.Types.Default,

    resize,
  };

  return methods;

  /**
   * Destroy the board.
   */
  function destroy() {
    assetsMethods.destroy();
    pieceMethods.destroy();
    board.reset();
  }

  /**
   * Gives a way by which the renderer may be extended
   */
  function extendRenderer(fn: Function) {
    Object.assign(methods, fn(params));
  }

  /**
   * Get the DOm pieces as an object.
   */
  function getPieces() {
    const pieces: HTMLElement[] = Array.from(el.querySelectorAll('.piece'));

    return formatPiecesAsObject(pieces);
  }

  /**
   * Handle a resize call.
   */
  function resize() {}
}
