import { ChessError } from 'chessboard/model/error';
import { ErrorCodes } from 'chessboard/model/error/types';
import type { ElementPool } from './config/types';
import { className } from './config/constants';
import './styles.scss';

/**
 * Create a pool of DOM elements.
 */
export function createElementPool({
  appendTo,
  elementType = 'div',
  insertBefore,
  startingCount = 0,
}: {
  appendTo?: Node;
  elementType?: string;
  insertBefore?: Node;
  startingCount?: number;
}): ElementPool {
  if (!appendTo && !insertBefore) {
    throw new ChessError({
      code: ErrorCodes.BadData,
      message:
        'When creating an element pool, you must provide an element to "appendTo" or "insertBefore".',
    });
  }

  const pool: HTMLElement[] = Array(startingCount)
    .fill(undefined)
    .map(createElement)
    .map(hideElement);

  return {
    destroy,
    get,
    put,
  };

  /**
   * Create a new element and attach it to the DOM.
   */
  function createElement(): HTMLElement {
    const el = document.createElement(elementType);

    el.className = className;

    if (appendTo) {
      appendTo.appendChild(el);
    } else {
      insertBefore?.parentNode?.insertBefore(el, insertBefore);
    }

    return el;
  }

  /**
   * Destroy the elements in the element pool.
   */
  function destroy() {
    pool.forEach((element) => element.parentNode?.removeChild(element));
    pool.length = 0;
  }

  /**
   * Get an element from the pool, or create a new one if necessary.
   */
  function get(): HTMLElement {
    const el = pool.pop() || createElement();

    return showElement(el);
  }

  /**
   * Hide an element.
   */
  function hideElement(el: HTMLElement): HTMLElement {
    for (const key in el.dataset) {
      if (el.dataset[key]) {
        el.dataset[key] = '';
      }
    }

    el.className = className;
    el.style.cssText = '';

    return el;
  }

  /**
   * Put an element back into the pool.
   */
  function put(el: HTMLElement): HTMLElement {
    hideElement(el);

    pool.push(el);

    return el;
  }

  /**
   * Show an element.
   */
  function showElement(el: HTMLElement): HTMLElement {
    el.className = '';

    return el;
  }
}
