import { Board } from 'chessboard/model/board/types';

/**
 * Get coordinates positions
 */
export function getCoordinates(position: Board.CoordinatesPositions) {
  if (position === Board.CoordinatesPositions.Outside) {
    return [
      { color: 'grey', fontSize: 3.1, x: 2, y: 3.5 },
      { color: 'grey', fontSize: 3.1, x: 2, y: 16 },
      { color: 'grey', fontSize: 3.1, x: 2, y: 28.5 },
      { color: 'grey', fontSize: 3.1, x: 2, y: 41 },
      { color: 'grey', fontSize: 3.1, x: 2, y: 53.5 },
      { color: 'grey', fontSize: 3.1, x: 2, y: 66 },
      { color: 'grey', fontSize: 3.1, x: 2, y: 78.5 },
      { color: 'grey', fontSize: 3.1, x: 2, y: 91 },
      { color: 'grey', fontSize: 3.1, x: 10.35, y: 99.25 },
      { color: 'grey', fontSize: 3.1, x: 22.85, y: 99.25 },
      { color: 'grey', fontSize: 3.1, x: 35.35, y: 99.25 },
      { color: 'grey', fontSize: 3.1, x: 47.85, y: 99.25 },
      { color: 'grey', fontSize: 3.1, x: 60.35, y: 99.25 },
      { color: 'grey', fontSize: 3.1, x: 72.85, y: 99.25 },
      { color: 'grey', fontSize: 3.1, x: 85.35, y: 99.25 },
      { color: 'grey', fontSize: 3.1, x: 97.85, y: 99.25 },
    ];
  }

  return [
    { color: 'light', fontSize: 2.8, x: 0.75, y: 3.5 },
    { color: 'dark', fontSize: 2.8, x: 0.75, y: 15.75 },
    { color: 'light', fontSize: 2.8, x: 0.75, y: 28.25 },
    { color: 'dark', fontSize: 2.8, x: 0.75, y: 40.75 },
    { color: 'light', fontSize: 2.8, x: 0.75, y: 53.25 },
    { color: 'dark', fontSize: 2.8, x: 0.75, y: 65.75 },
    { color: 'light', fontSize: 2.8, x: 0.75, y: 78.25 },
    { color: 'dark', fontSize: 2.8, x: 0.75, y: 90.75 },
    { color: 'dark', fontSize: 2.8, x: 10, y: 99 },
    { color: 'light', fontSize: 2.8, x: 22.5, y: 99 },
    { color: 'dark', fontSize: 2.8, x: 35, y: 99 },
    { color: 'light', fontSize: 2.8, x: 47.5, y: 99 },
    { color: 'dark', fontSize: 2.8, x: 60, y: 99 },
    { color: 'light', fontSize: 2.8, x: 72.5, y: 99 },
    { color: 'dark', fontSize: 2.8, x: 85, y: 99 },
    { color: 'light', fontSize: 2.8, x: 97.5, y: 99 },
  ];
}
