import { getAssetBasePath } from 'src/utils/get-asset-base-path';
import type { Board } from 'chessboard/model/board/types';

/**
 * Get the board URL.
 * Based on the expected path when board assets were hardcoded in chessboard
 */
export function getLegacyBoardURL(
  boardStylePath: string,
  size: number = 150,
  options?: Board.Options,
) {
  return `${getAssetBasePath(options)}/boards/${boardStylePath}/${size}.png`;
}
