import type { Board } from 'chessboard/model/board/types';
import { Game } from 'chessboard/model/game/types';
import { setTestData, TestElements, TestKeys } from 'src/utils/set-test-data';
import { getColorAsLetter } from 'chessboard/model/game/get-color-as-letter';
import { getFileRankFromSan } from 'chessboard/model/board/get-file-rank-from-san';
import { setPositionBySquare as setElPositionBySquare } from 'chessboard/model/board/set-position-by-square';
import type { Piece, PieceDetails } from 'chessboard/renderers/default/piece-movement/types';
import { PieceEvents } from 'chessboard/renderers/default/piece-movement/types';
import type { EventEmitterUtil } from '@chesscom/web-ui';

/**
 * Create a piece and add it to the DOM.
 */
export function createPiece({
  details,
  el,
  emitter,
  test,
}: {
  details: Game.Piece.Model;
  el: HTMLElement;
  emitter: EventEmitterUtil.EventEmitter;
  test?: boolean;
}): Piece {
  let shortString: string;
  const piece = { ...details };

  el.classList.add('piece');

  emitter.emit(PieceEvents.Created, { ...piece });

  setDetails(piece.color, piece.type);
  setPositionBySquare(<Board.SanSquare>piece.square);

  if (test) {
    setTestData(el, {
      [TestKeys.Element]: TestElements.Piece,
    });
  }

  return {
    el,
    getDetails,
    setDetails,
    setDraggingState,
    setPositionByCoords,
    setPositionBySquare,
  };

  /**
   * Generate the short string, e.g. 'wp', 'bk', etc.
   */
  function generateShortString(
    color: Game.ColorsAsNumbers,
    type: Game.Piece.ShortStrings.Black,
  ): string {
    return `${getColorAsLetter(color)}${type}`;
  }

  /**
   * Returns the details object.
   */
  function getDetails(): PieceDetails {
    return details;
  }

  /**
   * Sets new type and color for the piece. This adds the relevant class to the element
   * and saves the details.
   */
  function setDetails(color: Game.ColorsAsNumbers, type: Game.Piece.ShortStrings.Black): void {
    el.classList.remove(shortString);
    shortString = generateShortString(color, type);
    el.classList.add(shortString);
    details.type = type;
    details.color = color;

    emitter.emit(PieceEvents.DetailsSet, { ...details, shortString });

    if (test) {
      setTestData(el, {
        [TestKeys.Type]: type,
        [TestKeys.Color]: getColorAsLetter(color),
        [TestKeys.ShortString]: color === Game.ColorsAsNumbers.Black ? type : type.toUpperCase(),
      });
    }
  }

  /**
   * Raises a piece up above the rest with z-index.
   */
  function setDraggingState(drag?: boolean): void {
    if (drag) {
      el.classList.add('dragging');

      emitter.emit(PieceEvents.DragStarted, { ...details });

      if (test) {
        setTestData(el, {
          [TestKeys.Dragging]: 'true',
        });
      }

      return;
    }

    if (test) {
      setTestData(el, {
        [TestKeys.Dragging]: undefined,
      });
    }

    el.classList.remove('dragging');

    emitter.emit(PieceEvents.DragEnded, { ...details });
  }

  /**
   * Set the position of the piece by square.
   */
  function setPositionBySquare(square: Board.SanSquare, suspend?: boolean): void {
    if (test) {
      const fileRank = getFileRankFromSan(square);

      if (!fileRank) {
        return;
      }

      if (fileRank && !suspend) {
        setTestData(el, {
          [TestKeys.File]: fileRank.file.toString(),
          [TestKeys.Rank]: fileRank.rank.toString(),
          [TestKeys.Square]: square,
        });
      }
    }

    setElPositionBySquare({ el, square });

    emitter.emit(PieceEvents.PositionSetBySquare, { ...details });
  }

  /**
   * Set the position of the the piece by coords.
   */
  function setPositionByCoords(coords: Board.Coords): void {
    if (!coords) {
      return;
    }

    el.style.transform = `translate(${coords.x}%, ${coords.y}%)`;
  }
}
