import type { Board } from 'chessboard/model/board/types';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import { BoardPlaceholders } from 'chessboard/renderers/default/board/types';
import { setTestData, TestElements, TestKeys } from 'src/utils/set-test-data';
import { setPositionBySquare } from 'chessboard/model/board/set-position-by-square';

/**
 * Handlers related to move hints.
 */
export function createMoveHintsMethods({ board }: { board: BoardDOM }) {
  const hints: { [key: string]: HTMLElement } = {};

  return {
    addCaptureHints,
    addMoveHints,
    removeHints,
  };

  /**
   * Creates move hints and adds them to the DOM.
   */
  function addMoveHints(squares: Board.SanSquare[]): void {
    squares.forEach((square) => {
      if (hints[square]) {
        return;
      }

      hints[square] = createHintFromSquare(square);

      board.addToDom({ el: hints[square], type: BoardPlaceholders.MoveHints });
    });
  }

  /**
   * Creates hints for potential captures and adds them to the DOM.
   */
  function addCaptureHints(squares: Board.SanSquare[]): void {
    squares.forEach((square) => {
      if (hints[square]) {
        removeHint(square);
      }

      hints[square] = createHintFromSquare(square, { isPotentialCapture: true });

      board.addToDom({ el: hints[square], type: BoardPlaceholders.CaptureHints });

      hints[square].style.borderWidth = `${hints[square].clientWidth * 0.1}px`;
    });
  }

  /**
   * Removes all move hints on the given squares.
   */
  function removeHints(squares: Board.SanSquare[]): void {
    squares.forEach(removeHint);
  }

  /**
   * Creates a move hint element and sets the position.
   */
  function createHintFromSquare(
    square: Board.SanSquare,
    options: { isPotentialCapture?: boolean } = {},
  ): HTMLElement {
    const el = document.createElement('div');

    setTestData(el, {
      [TestKeys.Element]: options.isPotentialCapture
        ? TestElements.PotentialCapture
        : TestElements.Hint,
    });

    el.classList.add(options.isPotentialCapture ? 'capture-hint' : 'hint');

    setPositionBySquare({ el, square });

    return el;
  }

  /**
   * Removes a single move hint from the cache and the DOM.
   */
  function removeHint(square: Board.SanSquare): void {
    if (!hints[square]) {
      return;
    }

    const parent = hints[square].parentNode;

    (<HTMLElement>parent).removeChild(hints[square]);

    delete hints[square];
  }
}
