import type { Board } from 'chessboard/model/board/types';

/**
 * Gets coordinates (percentage-based) that are bounded inside the board for
 * dragging purposes. These are relative to a piece, not the board, and are
 * directly applicable to transform translate percentages.
 */
export function getCoordsInsideBoard(coords: Board.Coords): Board.Coords {
  let xInsideBoard = Math.max(coords.x, -50);
  xInsideBoard = Math.min(xInsideBoard, 750);
  let yInsideBoard = Math.max(coords.y, -50);
  yInsideBoard = Math.min(yInsideBoard, 750);

  return {
    x: xInsideBoard,
    y: yInsideBoard,
  };
}
