import type { Board } from 'chessboard/model/board/types';
import { createCollection } from 'utils/create-collection/create-collection';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import { BoardPlaceholders } from 'chessboard/renderers/default/board/types';
import { setTestData, TestElements, TestKeys } from 'src/utils/set-test-data';
import { setPositionBySquare } from 'chessboard/model/board/set-position-by-square';
import { ChessError } from 'chessboard/model/error';
import { ErrorCodes } from 'chessboard/model/error/types';
import { createElementPool } from 'src/utils/dom/element-pool/element-pool';
import type {
  KeyedBlinkingHighlight,
  KeyedHighlight,
} from 'chessboard/plugins/highlights/config/types';

export interface HighlightHandlers {
  addHighlights: (highlights: KeyedHighlight[]) => void;
  blinkHighlights: (blinkingHighlights: KeyedBlinkingHighlight[]) => void;
  removeHighlights: (highlights: KeyedHighlight[]) => void;
}

/**
 * Handlers related to highlighted squares.
 */
export function createHighlightsMethods({
  board,
  options,
}: {
  board: BoardDOM;
  options: Board.Options;
}): HighlightHandlers {
  const elementPool = createElementPool({
    insertBefore: board.placeholders.squares,
    startingCount: 3,
  });
  const highlightEls = createCollection<HTMLElement, Board.SanSquare>();
  const test = options.test;

  return {
    addHighlights,
    blinkHighlights,
    removeHighlights,
  };

  /**
   * Creates highlight elements and adds them to the board.
   */
  function addHighlights(highlights: KeyedHighlight[]): void {
    highlights.forEach((highlight) => {
      const { square } = highlight.data;

      if (highlightEls.isDefined(square) || !highlight) {
        return;
      }

      const highlightEl: HTMLElement = createHighlightEl(square, highlight);

      highlightEls.set(square, highlightEl);
    });
  }

  /**
   * Blink a single highlighted square that already exists in the DOM.
   */
  function blinkHighlight({
    el,
    interval,
    opacity,
    times,
  }: {
    el: HTMLElement;
    interval: number;
    opacity: number;
    times: number;
  }) {
    let count = 0;
    let on = true;

    const intervalVariable = setInterval(() => {
      count += 1;

      // Here we're switching both on and off, so we multiply the number
      // of blinks by 2 to get the total number of switches.
      if (count === times * 2) {
        clearInterval(intervalVariable);

        if (el) {
          const parent = el.parentNode;

          if (parent) {
            parent.removeChild(el);
          }
        }

        return;
      }

      if (!on) {
        el.style.opacity = opacity.toString();

        on = true;
      } else {
        el.style.opacity = '0';

        on = false;
      }
    }, interval / 2);
  }

  /**
   *  Blink a square.
   */
  function blinkHighlights(blinkingHighlights: KeyedBlinkingHighlight[]): void {
    blinkingHighlights.forEach((blinkingHighlight) => {
      const { color, interval, opacity, square, times } = blinkingHighlight.data;

      const blinkingHighlightEl = createHighlightEl(square, blinkingHighlight);

      board.addToDom({ el: blinkingHighlightEl, type: BoardPlaceholders.BlinkingHighlights });

      if (test) {
        setTestData(blinkingHighlightEl, {
          [TestKeys.Element]: TestElements.BlinkingHighlight,
          [TestKeys.Square]: square,
          [TestKeys.Color]: color,
          [TestKeys.Interval]: interval,
          [TestKeys.Opacity]: opacity,
          [TestKeys.Times]: times,
          [TestKeys.Type]: TestKeys.Blinking,
        });
      }

      blinkHighlight({
        el: blinkingHighlightEl,
        interval,
        opacity,
        times,
      });
    });
  }

  /**
   * Removes highlight squares from the board.
   */
  function removeHighlights(squares: KeyedHighlight[]): void {
    squares.forEach(removeHighlight);
  }

  /**
   * Creates a move highlight element and sets the position.
   */
  function createHighlightEl(san: Board.SanSquare, highlight: KeyedHighlight): HTMLElement {
    const el = elementPool.get();

    setTestData(el, {
      [TestKeys.Element]: TestElements.Highlight,
    });

    el.classList.add('highlight');

    updateHighlightElementStyles(el, highlight);

    setPositionBySquare({ el, square: san });

    return el;
  }

  /**
   * Removes a single move highlight from the cache and the DOM.
   */
  function removeHighlight(highlight: KeyedHighlight): void {
    const { square } = highlight.data;

    if (!highlightEls.isDefined(square)) {
      return;
    }

    const highlightEl = highlightEls.get(square);

    elementPool.put(highlightEl);

    highlightEls.deleteItem(square);
  }

  /**
   * Updates a single highlight square's styles if the new suware is different.
   */
  function updateHighlightElementStyles(el: HTMLElement, highlight: KeyedHighlight): void {
    if (!el) {
      throw new ChessError({
        code: ErrorCodes.ElementNotFound,
        data: { highlight },
        message: 'Highlight does not exist.',
      });
    }

    const {
      data: { color, opacity },
    } = highlight;

    const backgroundColor = color || '';
    const opacityStr = String(opacity);

    if (el.style.backgroundColor !== backgroundColor) {
      el.style.backgroundColor = backgroundColor;
    }

    if (el.style.opacity !== opacityStr) {
      el.style.opacity = opacityStr;
    }
  }
}
