import { calculateArrowLength } from 'chessboard/model/arrows/calculate-arrow-length';
import { calculateArrowSlope } from 'chessboard/model/arrows/calculate-arrow-slope';
import type { Core } from 'chessboard/core/config/types';
import { squareCoordinates } from 'chessboard/config/board';
import { KNIGHT_ARROW_LENGTH, validKnightArrowSlopes } from 'chessboard/config/arrows';
import { createStraightArrow } from 'chessboard/renderers/default/arrows/create-arrow/create-straight-arrow';
import { createKnightArrow } from 'chessboard/renderers/default/arrows/create-arrow/create-knight-arrow';
import { getArrowColor } from 'chessboard/model/arrows/get-arrow-color';
import type { KeyedArrow } from 'chessboard/plugins/arrows/config/types';

/**
 * Create arrows
 */
export function createArrow(
  arrow: KeyedArrow,
  context: Core.Context,
): SVGPolygonElement | undefined {
  if (!arrow.key) {
    return;
  }

  const { color, from: fromSan, opacity, to: toSan } = arrow.data;

  const polygon = document.createElementNS('http://www.w3.org/2000/svg', 'polygon');

  polygon.setAttribute('id', `arrow-${fromSan}${toSan}`);
  polygon.setAttribute('data-arrow', `${fromSan}${toSan}`);
  polygon.setAttribute('class', 'arrow');

  const { arrowColors } = context.options;

  polygon.style.fill = getArrowColor(color, arrowColors);

  if (opacity) {
    polygon.style.opacity = String(opacity);
  }

  const from = squareCoordinates[fromSan];
  const to = squareCoordinates[toSan];

  if (!from || !to) {
    return;
  }

  const length = calculateArrowLength({ from, to });
  const slope = calculateArrowSlope({ from, to });

  if (validKnightArrowSlopes.includes(slope) && length === KNIGHT_ARROW_LENGTH) {
    return createKnightArrow({ from, polygon, slope, to });
  }

  return createStraightArrow({ from, polygon, to });
}
