/**
 * Set test data on any element.
 */
export function setTestData(
  el: HTMLElement | SVGSVGElement | SVGPolygonElement,
  keyVals: KeyVals,
): boolean {
  if (!el) {
    return false;
  }

  Object.entries(keyVals).forEach(([key, val]) => {
    const capitalizedKey = `${key.charAt(0).toUpperCase()}${key.slice(1)}`;

    if (typeof val === 'undefined') {
      delete el.dataset[`test${capitalizedKey}`];
    } else {
      el.dataset[`test${capitalizedKey}`] = <string>val;
    }
  });

  return true;
}

interface KeyVals {
  [key: string]: string | number | undefined | boolean;
}

