import type { Board } from 'chessboard/model/board/types';
import { getSanFromFileRank } from 'chessboard/model/board/get-san-from-file-rank';

/**
 * Get the square that is bounded inside the board according to a FileRank. The
 * FileRank may be outside the board, but we need to return the nearest square
 * to that FileRank.
 */
export function getSquareInsideBoard(file: number, rank: number): Board.SanSquare | null {
  let fileInsideBoard = Math.max(file, 1);
  fileInsideBoard = Math.min(fileInsideBoard, 8);
  let rankInsideBoard = Math.max(rank, 1);
  rankInsideBoard = Math.min(rankInsideBoard, 8);

  return getSanFromFileRank({
    file: fileInsideBoard,
    rank: rankInsideBoard,
  });
}
