import type { Board } from 'chessboard/model/board/types';
import { getAbsoluteFileRankFromSan } from 'chessboard/model/board/get-absolute-file-rank-from-san';

/**
 * Get xy coordinates of the top-left of a square, as a percentage of the square size,
 * and from the top-left of the board.
 */
export function getCoordsFromSquare(square: Board.SanSquare, flipped: boolean): Board.Coords {
  const absoluteFileRank = getAbsoluteFileRankFromSan(square, flipped);

  return {
    x: absoluteFileRank.file * 100 - 100,
    y: (8 - absoluteFileRank.rank) * 100,
  };
}
