import { PointerEvents } from 'chessboard/model/dom/get-pointer-events';

export const defaultX = -1;
export const defaultY = -1;

let lastX = defaultX;
let lastY = defaultY;

/**
 * Get pure XY coordinates from event.
 * Useful because of many different devices/browsers/events.
 */
export function getXYFromEvent({ clientX, clientY, pointer, type, touches }: any): {
  x: number;
  y: number;
} {
  /**
   * Since `touchend` event doesn't contain any coordinates, we need to save and get them
   * from the previous events: `touchstart` and `touchmove`
   */
  const shouldTrack = type === PointerEvents.Touchmove || type === PointerEvents.Touchstart;
  if (shouldTrack && touches.length > 0) {
    lastX = touches[0].clientX;
    lastY = touches[0].clientY;
  }

  if (type === PointerEvents.Touchend) {
    return {
      x: lastX,
      y: lastY,
    };
  }

  if (pointer) {
    return {
      x: pointer.x,
      y: pointer.y,
    };
  }

  if (touches && touches.length > 0) {
    return {
      x: touches[0].clientX,
      y: touches[0].clientY,
    };
  }

  return {
    x: clientX ?? defaultX,
    y: clientY ?? defaultY,
  };
}
