import type { Board } from 'chessboard/model/board/types';
import type { Coordinate } from 'chessboard/renderers/default/coordinates/get-text-nodes/types';
import { getTextChar } from 'chessboard/renderers/default/coordinates/get-text-nodes/get-text-char';
import { getCoordinates } from 'chessboard/renderers/default/coordinates/get-text-nodes/get-coordinates';

/**
 * Get the SVG HTML.
 */
export function getTextNodes(position: Board.CoordinatesPositions, flipped: boolean) {
  const text = getTextChar(flipped);
  const coordinates = getCoordinates(position);

  return (coordinates as Coordinate[])
    .map((coordinate, ndx) => ({
      ...coordinate,
      text: text[ndx],
    }))
    .map(
      (coord) =>
        `<text 
          x="${coord.x}" 
          y="${coord.y}" 
          ${coord.fontSize ? `font-size="${coord.fontSize}"` : ''} 
          class="coordinate-${coord.color}">${coord.text}</text>`,
    )
    .join('');
}
