import 'chessboard/renderers/default/coordinates/style.scss';
import type { Renderer } from 'chessboard/renderers/config/types';
import { AnimationTypes, BoardClasses } from 'chessboard/renderers/config/types';
import type { Board } from 'chessboard/model/board/types';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import { BoardPlaceholders } from 'chessboard/renderers/default/board/types';
import type { Core } from 'chessboard/core/config/types';
import type { KeyedEffect } from 'chessboard/plugins/effects/config/types';
import { EffectType } from 'chessboard/plugins/effects/config/types';
import { setTestData, TestElements, TestKeys } from 'src/utils/set-test-data';
import { createExternallyResolvablePromise } from 'src/utils/promise/create-externally-resolvable-promise';
import { isEdgeSquare } from 'chessboard/plugins/effects/renderer/model/is-edge-square';
import { tuckMarking } from 'chessboard/plugins/effects/renderer/model/tuck-marking';

/**
 * Add placeholders to the board and provide a function to add elements
 * into the correct place in the DOM.
 */
export function createBoard({
  el,
  options,
  testElement = TestElements.Board,
}: {
  el: HTMLElement;
  options: Board.Options;
  testElement?: TestElements;
}): BoardDOM {
  const placeholders = {
    [BoardPlaceholders.Coordinates]: document.createComment('/Coordinates'),
    [BoardPlaceholders.Squares]: document.createComment('/Squares'),
    [BoardPlaceholders.BlinkingHighlights]: document.createComment('/Blinking Highlights'),
    [BoardPlaceholders.Effects]: document.createComment('/Effects'),
    [BoardPlaceholders.HoverSquare]: document.createComment('/Hover Square'),
    [BoardPlaceholders.Pieces]: document.createComment('/Pieces'),
    [BoardPlaceholders.MoveHints]: document.createComment('/MoveHints'),
    [BoardPlaceholders.CaptureHints]: document.createComment('/Capture Hints'),
    [BoardPlaceholders.Arrows]: document.createComment('/Arrows'),
    [BoardPlaceholders.PromotionWindow]: document.createComment('/Promotion Window'),
    [BoardPlaceholders.FadeSetup]: document.createComment('/Fade Setup'),
  };

  // Append placeholders
  Object.values(placeholders).forEach((placeholder) => el.appendChild(placeholder));

  let animationCompleteResolvablePromise = createExternallyResolvablePromise<boolean>();
  animationCompleteResolvablePromise.resolve?.(true);

  if (options.test) {
    setTestData(el, {
      [TestKeys.Element]: testElement,
    });
  }

  return {
    addToDom,
    animationComplete,
    el,
    flipBoard,
    setBoardEnabled,
    isAnimating,
    isFlipped,
    placeholders,
    setAnimatingStatus,
    reset,
  };

  /**
   * Helper function to add an element into the correct place in the DOM.
   */
  function addToDom({
    type,
    el: element,
    insertAfter = false,
  }: {
    type: BoardPlaceholders;
    el: Comment | HTMLElement | SVGSVGElement;
    insertAfter?: boolean;
  }): void {
    if (insertAfter) {
      el.insertBefore(element, placeholders[type].nextSibling);
    } else {
      el.insertBefore(element, placeholders[type]);
    }
  }

  /**
   * Flip the board.
   */
  function flipBoard(shouldFlip: boolean, context?: Core.Context): void {
    el.classList.toggle(BoardClasses.Flipped, shouldFlip);

    if (context?.options.allowMarkings) {
      const effects = context.api.markings.getAllWhere({ types: [EffectType] });

      if (effects.length > 0) {
        updateTuckedEffects(effects, context.renderer);
      }
    }
  }

  /**
   * Reset board
   */
  function reset(): void {
    el.innerHTML = '';
    el.classList.remove(BoardClasses.Flipped);
  }

  /**
   * Update effects that have been tucked.
   */
  function updateTuckedEffects(effects: KeyedEffect[], renderer: Renderer.Model | undefined) {
    effects.forEach((effect) => {
      const {
        data: { square },
        key,
      } = effect;
      const effectEl = renderer?.getEffectElements?.().get(key);

      if (!effectEl) {
        return;
      }

      effectEl.forEach((effEl) => {
        if (effEl.classList.contains('tuck-right') || effEl.classList.contains('tuck-top')) {
          effEl.classList.remove('tuck-right');
          effEl.classList.remove('tuck-top');
        }
      });

      if (isEdgeSquare(square)) {
        effectEl.forEach((effEl) => {
          tuckMarking({
            square,
            isFlipped: el.classList.contains('flipped'),
            effectEl: effEl,
          });
        });
      }
    });
  }

  /**
   * Empty function.
   */
  function setBoardEnabled(): void {}

  /**
   * Determine whether or not the board is in an animating state.
   */
  function isAnimating(): boolean {
    return Boolean(el.dataset.testAnimating);
  }

  /**
   * Return a promise that will resolve when an animation is complete.
   */
  function animationComplete(animationType: AnimationTypes): Promise<void | boolean> {
    if (![AnimationTypes.All, AnimationTypes.Move].includes(animationType)) {
      return Promise.resolve(true);
    }

    return animationCompleteResolvablePromise.promise;
  }

  /**
   * Returns true if the board is flipped.
   */
  function isFlipped(): boolean {
    return options.flipped;
  }

  /**
   * Set the animating status.
   */
  function setAnimatingStatus(isInAnimatingState: boolean): void {
    if (!isInAnimatingState) {
      delete el.dataset.testAnimating;
      animationCompleteResolvablePromise.resolve?.(true);
    } else {
      el.dataset.testAnimating = 'true';
      animationCompleteResolvablePromise = createExternallyResolvablePromise<boolean>();
    }
  }
}
