/* eslint-disable @typescript-eslint/no-unused-vars */
import 'chessboard/renderers/default/arrows/style.scss';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';
import { BoardPlaceholders } from 'chessboard/renderers/default/board/types';
import type { Core } from 'chessboard/core/config/types';
import { createArrow } from 'chessboard/renderers/default/arrows/create-arrow';
import type { KeyedArrow } from 'chessboard/plugins/arrows/config/types';

/**
 * Handlers related to arrows.
 */
export function createArrowsMethods({ board }: { board: BoardDOM }) {
  const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');

  svg.setAttribute('viewBox', '0 0 100 100');
  svg.classList.add('arrows');

  board.addToDom({ el: svg, type: BoardPlaceholders.Arrows });

  const arrowElements: Map<string, SVGPolygonElement> = new Map();

  return {
    addArrows,
    removeArrows,
  };

  /**
   * Add arrows to the DOM.
   */
  function addArrows(arrows: KeyedArrow[], context: Core.Context) {
    arrows.forEach((arrow) => {
      const arrowToAdd = createArrow(arrow, context);

      if (arrowToAdd) {
        svg.appendChild(arrowToAdd);
        arrowElements.set(arrow.key, arrowToAdd);
      }
    });
  }

  /**
   * Remove arrows from the DOM.
   */
  function removeArrows(arrows: KeyedArrow[]) {
    arrows.forEach((arrow) => {
      const { key } = arrow;
      const arrowEl = arrowElements.get(key);

      if (!arrowEl) {
        return;
      }

      svg.removeChild(arrowEl);
      arrowElements.delete(key);
    });
  }
}
