import type { Board } from 'chessboard/model/board/types';

/**
 * Calculate the slope between two points
 */
export function calculateArrowSlope({
  from,
  to,
}: {
  from: Board.Coords;
  to: Board.Coords;
}): number {
  const slope = (from.y - to.y) / (from.x - to.x);
  const roundedSlope = Number(slope.toFixed(1));

  return roundedSlope;
}
