import type { Board } from 'chessboard/model/board/types';
import type { Core } from 'chessboard/core/config/types';
import { createStyleDOM } from 'chessboard/renderers/default/assets/style-dom';
import type { LegacyThemes } from 'chessboard/config/legacy-themes';
import { BoardClasses } from 'chessboard/renderers/config/types';
import type { ThemeAssets } from 'chessboard/config/theme-assets';
import { validateBoardOptions2D } from 'chessboard/config/theme-validation-utils';
import { isPseudo3d } from 'chessboard/config/is-pseudo-3d';

/**
 * Handlers related to assets.
 */
export function createAssetsMethods({
  boardStyles,
  el,
  options,
  pieceStyles,
}: {
  boardStyles: LegacyThemes.BoardStyles;
  el: HTMLElement;
  options: Board.Options<ThemeAssets.Assets2D>;
  pieceStyles: LegacyThemes.PieceStyles;
}) {
  const styleDOM = createStyleDOM({ boardStyles, options, pieceStyles });

  return {
    destroy,
    updateBoardImage,
    updatePieceBaseImage,
    togglePseudo3d,
    getPieceStyles: styleDOM.getPieceStyles,
  };

  /**
   * Destroy style DOM.
   */
  function destroy(): void {
    styleDOM.destroy();
  }

  /**
   * Set the contents of the style tag according to the current options.
   */
  function updateBoardImage(context: Core.Context): void {
    styleDOM.updateStyles(validateBoardOptions2D(context.options));
  }

  /**
   * Set the contents of the style tag according to the current options.
   */
  function updatePieceBaseImage(context: Core.Context, fallback = false): void {
    togglePseudo3d(context.options);

    styleDOM.updateStyles(validateBoardOptions2D(context.options), fallback);
  }

  /**
   * Toggle pseudo 3d class on board element depending on piece style.
   */
  function togglePseudo3d(boardOptions: Board.Options): void {
    const showClass = isPseudo3d(boardOptions, pieceStyles);

    el.classList.toggle(BoardClasses.Pseudo3d, showClass);
  }
}
