import { removeClassNamesStartingWith } from 'utils/dom/remove-class-names-starting-with';
import type { Board } from 'chessboard/model/board/types';
import { getFileRankFromSan } from 'chessboard/model/board/get-file-rank-from-san';

/**
 * Sets an element's transform coordinates to align with a given square.
 */
export function setPositionBySquare({
  square,
  el,
}: {
  square: Board.SanSquare;
  el: HTMLElement;
}): void {
  const fileRank = getFileRankFromSan(square);

  if (!fileRank) {
    return;
  }

  el.className = removeClassNamesStartingWith(el.className, 'square-');

  el.classList.add(`square-${fileRank.file}${fileRank.rank}`);

  el.style.transform = '';
}
