import 'chessboard/renderers/default/analysis-overlay/style.scss';
import type { BoardDOM } from 'chessboard/renderers/default/board/types';

/**
 * Create analysis overlay.
 */
export function createAnalysisOverlayMethods({ board }: { board: BoardDOM }) {
  const { classList } = board.el;

  return {
    addAnalysisOverlay,
    removeAnalysisOverlay,
  };

  /**
   * Add addAnalysisOverlay.
   */
  function addAnalysisOverlay() {
    classList.add('analysis-overlay');
  }

  /**
   * Add removeAnalysisOverlay.
   */
  function removeAnalysisOverlay() {
    classList.remove('analysis-overlay');
  }
}
